@import url('https://static2.sharepointonline.com/files/fabric/office-ui-fabric-core/9.6.0/css/fabric.min.css');

body {
  padding: 0;
}

@font-face {
  font-family: 'segoe assets';
  src: url(../public/SEGMDL2.ttf) format('truetype');
}


.App {
  margin: 0 20px;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.toolbar-rooster {
  border-bottom: 2px solid rgba(234,234,234,1) !important;
  z-index: 1 !important;
}

.loose-focus {
  width: 776px;
  height: 400px;
  padding-top: 0;
  /* width: 776px;
  min-height: 500px;
  padding: 8px 20px 16px 20px; */
}

/* div#roosterParent-undefined {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
} */

.roosterwrap-text-like {
  border: 2px solid rgba(234,234,234,1);
}

#container-abc {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


element.style {
  user-select: text;
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
}
@media only screen and (max-width: 1024px) {
  .loose-focus {
    min-height: inherit;
    padding-top: 0px;
  }
}