/* Rooster Class Starts*/
.roosterwrap-button {
    background: white;
    border: none;
    box-shadow: none;
    font-family: 'Segoe UI' !important;
    font-size: 14px !important;
}

.roosterwrap-button:hover {
    background: #7c1c58;
    color: white;
    cursor: pointer;
}

.rooster-box {
    width: 100%;
    height: 400px;
    border: solid 1px black;
    overflow: auto;
    padding: 20px;
    border-top: 4px;
}

.roosterwrap-text-like {
    /* border: 1px solid rgb(23, 23, 23); */
    min-height: 120px;
    resize: vertical;
    min-width: 100%;
    max-width: 100%;
    word-wrap: break-word;
    white-space: normal;
    font-size: 15px;
    color: #000;
    background: #fff;
    overflow-y: auto;
}

.padding-8px {
    padding: 8px 8px 8px 8px;
}

.toolbar-rooster {
    /* display: none; */
    width: inherit;
    border-bottom: none;
    padding: 8px 8px 2px 8px;
    background-color: #ffffff;
    position: sticky;
    top: 0px;
}

