.just-img {
    background: none !important;
    cursor: inherit;
    background-repeat: no-repeat;
    width: 16px;
    height: 16px;
    display: inline-block;
    margin: 0;
    padding: 0;
    border: 0;
    background: transparent;
    text-decoration: none;
    vertical-align: baseline;
    box-sizing: content-box;
    position: static;
    transition: none;
    border-collapse: collapse;
    font: normal normal normal 12px Arial, Helvetica, Tahoma, Verdana, Sans-Serif;
    color: #000;
    text-align: left;
    white-space: nowrap;
}

.just-img-padding {
    padding: 4px 6px 4px 6px;
    cursor: pointer;
    text-decoration: none !important;
    margin-left: 4px;
    border: 0.5px solid #ffffff;
}

.just-image-pad-selection {
    border: 0.5px solid #cecece !important;
}

.just-img-padding:hover {
    border: 0.1px solid #cecece;
}

.just-img-common {
    font-weight: normal;
    font-size: 18px !important;
    padding-top: 0px !important;
    line-height: 0.5;
}

.just-img-common-second {
    font-size: 14px !important;
    padding-right: 0px !important;
}

.just-font-familia-img:before {
    content: '\E185';
}

.just-font-sizia-img:before {
    content: '\E164';
}
.just-bold-img:before {
    content: 'B';
}

.just-bold-img-more {
    font-weight: bold;
}

.just-italics-img:before {
    content: 'I';
}

.just-italics-img-more {
    font-style: italic;
}

.just-underline-img:before {
    content: 'U';
    text-decoration: underline;
}

.just-undo-img:before {
    content: '\E7A7';
    font-size: 14px !important;
}

.just-redo-img:before {
    content: '\E7A6';
    font-size: 14px !important;
}

.just-unset-indentation-img:before {
    content: '\E290';
}

.just-indentation-img:before {
    content: '\E291';
}

.just-table-img:before {
    content: '\E80A';
}

.just-textcolo-img:before {
